<template>
<div id="adsuccess">
  <head-top title="Add Device Sucess"></head-top>
  <img src="../../../assets/addevice/succeed@3x.png" alt="">
  <span>Devices successfully added</span>
  <div class="button" @click="Start">
    Start Immediately
  </div>
</div>
</template>
<script>
import headTop from '../../../components/common/head'
import {mapGetters, mapActions} from 'vuex'
import { getStore, setStore } from '../../../utils'
import {
// commonPatternListDataRequest,
// temperatureAndHumidityDataRequest,
  deviceListRequest
} from '../../../api/method'
import {
// commonPatternListDataResponse,
// temperatureAndHumidityDataResponse
} from '../../../api/getData'
export default {
  data () {
    return {
      jumpPath: '', // 跳转路由
      macs: '' // 房间设备mac串
    }
  },
  components: {
    headTop
  },
  computed: {
    ...mapGetters(['addevicePath', 'getcurrentMac', 'getDevicelist', 'roomName']),
    getaddevicePath () {
      return this.addevicePath
    },
    currentDeviceMAC () {
      return this.getcurrentMac
    },
    deviceList () {
      return this.getDevicelist
    },
    getRooomName () {
      return this.roomName
    }
  },
  created () {
    // 定时数据
    // commonPatternListDataRequest(this.NAVIGATOR, this, this.currentDeviceMAC)

    // 温湿度数据
    // temperatureAndHumidityDataRequest(this.NAVIGATOR, this, this.currentDeviceMAC)
  },
  mounted () {
    this.jumpPath = this.getaddevicePath
    // commonPatternListDataResponse(this.NAVIGATOR, this)
    // temperatureAndHumidityDataResponse(this.NAVIGATOR, this)
  },
  methods: {
    ...mapActions(['setDevicelist']),
    // go () {
    //   this.$router.push({'path': this.getaddevicePath})
    //   console.log(this.getaddevicePath)
    // }
    Start () {
      // if (this.getaddevicePath === '/Me') {
      //   this.$router.push({'path': this.getaddevicePath})
      // } else {
      //   this.$router.push({'path': this.getaddevicePath})
      //   let roomData = JSON.parse(getStore('roomData'))
      //   let deviceList = this.deviceList
      //   deviceList.filter(item => { // 从设备列表删除添加设备
      //     return item.mac !== this.currentDeviceMAC
      //   })
      //   this.setDevicelist(deviceList)
      //   roomData.forEach(element => {
      //     if (element.name === this.getRooomName) {
      //       element.mac = element.mac + '&' + this.currentDeviceMAC
      //     }
      //   })
      //   setStore('roomData', roomData)
      //   // this.$router.push({'name': 'rooms', 'params': {'device':this.getRooomName , 'macs': this.macs}})
      // }
      this.$router.push({'path': '/Home/device'})
    }
  },
  watch: {
    jumpPath (item) {
      if (item === '/Me/rooms') {
        let deviceList = this.deviceList
        let roomList = JSON.parse(getStore('roomData'))
        deviceList = deviceList.filter(item => { // 从设备列表删除添加设备
          console.log(item.mac)
          console.log('删除列表设备不成功标记')
          if (item.mac !== this.currentDeviceMAC) {
            return item
          }
        })
        console.log(deviceList)
        this.setDevicelist(deviceList)
        if (roomList) {
          roomList.forEach(element => {
            if (element.name === this.getRooomName) {
              element.mac = element.mac + '&' + this.currentDeviceMAC
            }
          })
        }
        setStore('roomData', roomList)
      }
    }
  },
  destroyed () {
    deviceListRequest(this.NAVIGATOR, this)
  }
}
</script>

<style lang="less" scoped>
#adsuccess{
  .wh(100%, 100%);
  .fs;
  z-index: 999;
  background: white;
  display: flex;
  flex-direction: column;
  // justify-content: center;
  img{
    display: block;
    .wh(208px,208px);
    margin-top: 140px;
    align-self: center;
  }
  span{
    margin-top: 84px;
    font-size: 33px;
  }
  .button{
    margin-top: 220px;
  }
}
</style>



